.App {
  height: 100%;
}

.app-header {
  background-color: #45515b;
  min-height: 60px;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

.header-container {
  display: flex;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .header-container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .header-container {
    width: 970px;
  }
}

@media (min-width: 1104px) {
  .header-container {
    width: 1054px;
  }
}

.app-logo {
  height: 40vmin;
  pointer-events: none;
  margin: 0;
  width: 134px;
  height: 70px;
  flex: 0 0 auto;
  display: flex;
}

.breaker {
  flex: 1;
  opacity: 0;
}

.app-profile {
  flex: 0 0 auto;
  display: flex;
  padding: 5px;
  margin-right: 15px;
  align-items: center;
  background-color: #354052;

  ion-icon {
    background-color: #4c5c79;
    padding: 8px;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
    &:hover {
      background-color: #343f42;
    }

    &:first-child {
      margin-right: 8px;

      &.dot::before {
        content: "1";
        height: 15px;
        width: 15px;
        background: #d84c3a;
        border-radius: 50%;
        position: absolute;
        right: 0;
        font-size: 10px;
        top: 0;
        text-align: center;
      }
    }
  }
}

.aligned {
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-background {
  background: #354052;
  padding: 5px 8px;
  position: relative;
}

.profile-img {
  height: 50px;
  width: 50px;
  border-radius: 30px;
  background-image: url("https://thispersondoesnotexist.com/image");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  &::before {
    background-image: url("./resources/profile-pseudo.png");
    position: absolute;
    bottom: 2px;
    right: 0px;
    content: " ";
    background-size: 100% 100%;
    width: 35%;
    height: 35%;
  }
}

.app-body {
  background-color: white;
  height: calc(100% - 60px);
}

.App-link {
  color: #61dafb;
}

.ui-container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .ui-container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .ui-container {
    width: 970px;
  }
}

@media (min-width: 1104px) {
  .ui-container {
    width: 1054px;
  }
}

.light {
  font-weight: 100;
  font-size: 18px;
}

.horizontal-list {
  display: flex;
  flex-flow: row wrap;

  justify-content: space-between;

  padding: 0;
  margin: 0;
  list-style: none;
  padding: 0px 15px;

  > li {
    flex: 0;
    color: #424036;
    opacity: 0.7;
  }
}

.flex-container {
  display: flex;
  flex-flow: row wrap;

  justify-content: space-between;

  padding: 0;
  margin: 0;
  list-style: none;

  > li {
    padding: 5px;
    width: 230px;
    height: 230px;
    margin-top: 10px;
    color: white;
  }

  .map {
    background-image: url("./resources/map.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .bro {
    background-image: url("./resources/bro.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .bro2 {
    background-image: url("./resources/bro2.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .camera {
    font-weight: bold;
    text-align: center;
  }

  .double {
    width: 500px;
  }

  .zoom {
    transition: transform 0.5s;
    backface-visibility: hidden;
    &:hover {
      transform: scale(1.048);
    }
  }
}

.profit {
  font-size: 40px;
  font-weight: 700;
  font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

.chart {
  position: relative;
  background: #ffba00;
  padding: 25px;
}
.lines {
  height: 80px;
}
.line-chart {
  list-style: none;
  margin: 0;
  padding: 0;
}

.data-point {
  background-color: #424036;
  border: 2px solid #424036;
  border-radius: 50%;
  bottom: calc(var(--y) - 5px);
  height: 6px;
  left: calc(var(--x) - 5px);
  position: absolute;
  width: 6px;
  z-index: 1;
}

.line-segment {
  background-color: #424036;
  bottom: var(--y);
  height: 2px;
  left: var(--x);
  position: absolute;
  transform: rotate(calc(var(--angle) * 1deg));
  transform-origin: left bottom;
  width: calc(var(--hypotenuse) * 1px);
}

.text-tile {
  padding: 15px 15px 0 15px;
}

.no-padding {
  padding: 0;
  margin: 0;
}

.small {
  font-size: 9px;
}

.card {
  box-shadow: 1px 0px 5px 0px rgba(0, 0, 0, 0.75);
  color: #505050 !important;
  padding: 0px 12px;
}

.yellow {
  color: #ffba00;
}

.menu {
  position: absolute;
  background-color: white;
  top: 58px;
  right: 30px;
  box-shadow: 1px 0px 5px 0px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  color: #505050 !important;
  padding: 15px;
  width: 150px;
}

.profile-container {
  position: relative;
}
